import { Map } from 'immutable';
import { getCandidateCoords } from '../utils';

export default class Minimax {
	state: any;
	constructor(state: any) {
		this.state = state;
	}

	makeBlindMove() {
		const candidateCoords = getCandidateCoords(this.state, Map({
			x: 0,
			y : 0
		}));
		return candidateCoords.get(0);
	}
}


